<template>
  <el-dialog
    title="hexo "
    :visible.sync="isVisible"
    @closed="hasModalClosed"
  >
    <el-form
      label-width="160px"
      ref="hexoForm"
      :model="hexoItem"
    >
      <el-alert
        class="tips-item"
        :title="tips"
        type="warning"
        show-icon
        :closable="false"
      ></el-alert>
      <el-form-item label="hexo " prop="source">
        <el-input v-model="hexoItem.source"></el-input>
      </el-form-item>
      <el-form-item label="" prop="categories">
        <el-input placeholder=" , " v-model="hexoItem.categories"></el-input>
      </el-form-item>
      <el-form-item label="" prop="tags">
        <el-input placeholder=" , " v-model="hexoItem.tags"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="openModal(false)"></el-button>
      <el-button type="primary" @click="save"></el-button>
    </div>
  </el-dialog>
</template>

<script>
/**
 * hexo 
 */
import { mapGetters, mapActions } from 'vuex'

export default {
  data () {
    return {
      tips: ' hexo  hexo ',
      isVisible: false, // 
      hexoItem: { // hexo 
        source: '',
        categories: '',
        tags: '',
      },
    }
  },

  computed: {
    ...mapGetters('config', [
      'hexoObject',
    ])
  },

  methods: {
    ...mapActions('config', [
      'getConfig',
      'saveConfig',
    ]),

    // 
    initModal () {
      // 
      this.getConfig().then(isSuccess => {
        const { source, categories, tags } = this.hexoObject
        this.hexoItem.source = source
        this.hexoItem.categories = categories
        this.hexoItem.tags = tags
      })
    },

    // 
    openModal (isShow = true) {
      if (isShow) {
        // 
        this.initModal()
      }
      this.isVisible = isShow
    },

    // 
    save () {
      this.$refs.hexoForm.validate(valid => {
        if (!valid) {
          return
        }
        const { source, categories, tags } = this.hexoItem
        const data = { source, categories, tags }
        this.saveConfig(data).then(() => {
          this.openModal(false)
        }).catch(() => {})
      })
    },

    // 
    hasModalClosed () {
      this.$refs.hexoForm.resetFields()
    },
  },
}
</script>

<style scoped>
.tips-item {
  margin-bottom: 30px;
}
</style>



